.dashboard .photo-uploader {
  margin: 0 auto;
  width: 300px;
  height: 300px;
}
.dashboard .photo-uploader > .ant-upload {
  width: 100%;
  height: 100%;
  -moz-box-sizing: border-box;
       box-sizing: border-box;
  padding: 0;
}
.dashboard .preview-img {
  width: 280px;
  height: 280px;
  margin: auto;
  display: -moz-box;
  display: flex;
  -moz-box-align: center;
       align-items: center;
  -moz-box-pack: center;
       justify-content: center;
  overflow: hidden;
}
.dashboard .preview-img img {
  width: 100%;
}
