.basic-layout {
  height: 100%;
  min-width: 1100px;
}
.basic-layout .trigger {
  font-size: 18px;
  line-height: 48px;
  padding: 0 16px;
  cursor: pointer;
  transition: color 0.3s;
}
.basic-layout .trigger:hover {
  color: #1890ff;
}
.basic-layout .logo {
  height: 48px;
  margin: 0 8px;
  display: -moz-box;
  display: flex;
  -moz-box-align: center;
       align-items: center;
  -moz-box-sizing: border-box;
       box-sizing: border-box;
  -moz-box-pack: center;
       justify-content: center;
  cursor: pointer;
}
.basic-layout .logo .logo-img {
  display: inline-block;
  width: 32px;
  height: 32px;
  font-size: 0;
}
.basic-layout .logo .title {
  display: inline-block;
  margin: 0 0 0 12px;
  color: #fff;
  font-weight: 600;
  font-size: 22px;
  vertical-align: middle;
  -webkit-animation: fade-in;
          animation: fade-in;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
  -moz-box-sizing: border-box;
       box-sizing: border-box;
}
.basic-layout .site-layout .site-layout-background {
  background: #fff;
}
.basic-layout .site-content {
  margin: 8px;
  padding: 8px;
  border-radius: 4px;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  min-height: -webkit-max-content;
  min-height: -moz-max-content;
  min-height: max-content;
}
.basic-layout .site-header {
  height: 48px;
  line-height: 48px;
  position: relative;
}
.basic-layout .user-box {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  display: -moz-box;
  display: flex;
  z-index: 1;
  -moz-box-align: center;
       align-items: center;
  -moz-box-sizing: border-box;
       box-sizing: border-box;
  -moz-box-pack: right;
       justify-content: right;
}
.basic-layout .user-box .user-info {
  height: 100%;
  position: relative;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  padding: 0 8px;
}
