.userlayout {
  height: 100%;
  position: relative;
}
.userlayout .container {
  width: 100%;
  min-height: 100%;
  background: #f0f2f5 url(./static/media/background.5825f033.svg) no-repeat 50%;
  background-size: 100%;
  padding: 130px 0 144px;
  position: relative;
}
.userlayout .container .top {
  text-align: center;
}
.userlayout .container .top .logo {
  height: 44px;
  vertical-align: top;
  margin-right: 16px;
  border-style: none;
}
.userlayout .container .top .header {
  height: 44px;
  line-height: 44px;
  font-size: 33px;
  color: rgba(0, 0, 0, 0.85);
  font-weight: 600;
}
.userlayout .container .top .header .title {
  top: 2px;
  position: relative;
}
.userlayout .container .top .desc {
  font-size: 14px;
  color: rgba(0, 0, 0, 0.45);
  margin-top: 12px;
  margin-bottom: 40px;
}
.userlayout .container .main {
  min-width: 260px;
  width: 368px;
  margin: 0 auto;
}
.userlayout .container .main .login-form-button {
  width: 100%;
}
.userlayout .footer-msg {
  position: fixed;
  z-index: 1;
  bottom: 0;
  width: 100%;
  left: 0;
  padding: 12px;
  border-top: 1px solid #eee;
  text-align: center;
  line-height: 1.4;
  color: #666;
  font-size: 12px;
}
.userlayout .footer-msg a {
  line-height: inherit;
  color: inherit;
  font-size: inherit;
}
